const reducer = (state, action) => {
  const newState = (() => {
    switch (action.type) {
      case "ADD_USER": return { ...state, users: [...state.users, action.payload] };
      case "UPDATE_USER": return { ...state, users: state.users.map(u => u.email === action.payload.email ? action.payload : u) };
      case "ADD_CROP": return { ...state, crops: [...state.crops, action.payload], agriPoints: state.agriPoints + 25, greenCredits: state.greenCredits + 15 };
      case "UPDATE_CROP": return { ...state, crops: state.crops.map((c, i) => i === action.index ? action.payload : c) };
      case "ADD_MESSAGE": return { ...state, messages: [...state.messages, action.payload] };
      case "UPDATE_BALANCE": return { ...state, walletBalance: Math.max(0, state.walletBalance + action.amount) }; // Fixed: Added dot between state and walletBalance
      case "ADD_TRANSACTION": return { ...state, transactions: [...state.transactions, action.payload] };
      case "SET_SOIL_DATA": return { ...state, soilData: action.payload };
      case "UPDATE_MARKET_DATA": return { ...state, marketData: action.payload };
      case "UPDATE_ANALYTICS": return { ...state, analytics: action.payload };
      case "UPDATE_SETTINGS": return { ...state, settings: action.payload };
      default: return state;
    }
  })();
  Object.entries(newState).forEach(([key, value]) => localStorage.setItem(key, JSON.stringify(value)));
  return newState;
};

